<template>
  <div class="app-container">
    <el-descriptions title="单据详情">
      <template #extra>
        <el-button type="primary" :disabled="baseInfo.canStockOutMatarialCount <= 0" @click="handleOutWarehouse">出库</el-button>
      </template>
    </el-descriptions>
    <el-row>
      <el-descriptions :column="4" style="margin-bottom: 10px;">
        <el-descriptions-item label="单据类型">
          <el-tag v-show="params.billType" :type="typeMap[params.billType]">
            {{ {1: '预留单',2: '领料单',3: '退货单',4: '外协单',5: '调拨单'}[params.billType] }}
          </el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="单号"> {{ params.billNumber }}</el-descriptions-item>
        <el-descriptions-item label="日期"> {{ baseInfo.needDate || '---' }}</el-descriptions-item>
        <el-descriptions-item label="项目"> {{ baseInfo.project || '---' }}</el-descriptions-item>
        <el-descriptions-item label="总账科目"> {{ baseInfo.ledgerSubjects || '---' }}</el-descriptions-item>
      </el-descriptions>
    </el-row>
    <material-list />
    <!-- <el-descriptions :column="3">
      <el-descriptions-item label="到货数量">{{ '---' }}</el-descriptions-item>
      <el-descriptions-item label="入库数量">{{ '---' }}</el-descriptions-item>
      <el-descriptions-item label="未入库数量">{{ '---' }}</el-descriptions-item>
    </el-descriptions> -->
    <!-- 表格展示 -->
    <el-descriptions title="已出库信息" />
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="billNumber">
          <el-input v-model.trim="query.billNumber" clearable placeholder="单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        ref-name="baseTableRef"
        table-height-type="fullscreenTab"
        has-top-query
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="245"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleDetail(scope.row)">详情</el-button>
          <el-button type="primary" @click="handlePrintTemplate(scope.row)">打印</el-button>
          <!-- <el-button type="danger" @click="handleDelete(scope.row)">删除</el-button> -->
        </template>
      </table-com>
    </el-row>
    <!-- 分页组件 -->
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <print-templates v-model="dlgVisible" title="打印模板" :extra-info="curRow" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import tags from '@/mixins/tags'

import { getListApi, printApi } from '@/apis/modules/out-bound/outbound-information-detail'
import { getMaterialDetailTopApi, getReservationDetailTopApi, getReturnDetailTopApi } from '@/apis/modules/out-bound/outbound-information'
import { getBillTransferDetailApi as getBillTransferDetailTopApi } from '@/apis/modules/warehouse/discharge'
import { getDetailApi as getOutsourceDetailTopApi } from '@/apis/modules/out-bound/outsourcing'

import MaterialList from './MaterialList'
import PrintTemplates from './PrintTemplates'

export default {
  // name: 'OutBoundInfoDetail',
  components: {
    MaterialList,
    PrintTemplates
  },
  mixins: [tags, paginationPage],
  data() {
    return {
      loading: false,
      query: {
        billNumber: ''
      },
      typeMap: {
        1: 'success', // 预留单
        2: 'primary', // 领料单
        3: 'warning', // 退货单
        4: 'info', // 外协单
        5: 'danger' // 调拨单
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'billNumber',
          label: '单号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'accountingDate',
          label: '日期',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'source',
          label: '去向(客户或生产单位)',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'storekeeper',
          label: '库管员',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'printTimes',
          label: '打印次数',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      params: {}, // 路由参数
      baseInfo: {}, // 基本信息
      dlgVisible: false, // 打印模板弹窗
      curRow: {} // 当前点击的入库单信息
    }
  },
  mounted() {
    this.params = this.$route.params
    this.setTagsTitleMixins(null, this.params.billNumber)
    this.init()
    this.initInfo()
  },
  methods: {
    init() {
      console.log('init method')
      this.loading = true
      const params = {
        ...this.query,
        affiliatedBillNumber: this.params.billNumber,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip 初始化来源单(预留单 | 领料单 | 退货单 | 外协单 | 调拨单)基础信息
    initInfo() {
      const api = {
        1: getReservationDetailTopApi, // 预留单
        2: getMaterialDetailTopApi, // 领料单
        3: getReturnDetailTopApi, // 退货单
        4: getOutsourceDetailTopApi, // 外协单
        5: getBillTransferDetailTopApi // 调拨单
      }[this.params.billType]
      if (!api) return
      return new Promise((resolve, reject) => {
        const params = {
          ...this.params.billType === '1' && { subNumber: this.params.billNumber },
          ...['2', '3', '4', '5'].includes(this.params.billType) && { billNumber: this.params.billNumber }
        }
        api({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseInfo = data
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    // 批量删除
    handleDelete(item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        // deleteListApi({
        //   entity: {
        //     id: item.id
        //   }
        // })
        //   .then(res => {
        //     const { code } = res
        //     if (!code) {
        //       this.$notify.success({
        //         title: '成功',
        //         message: `删除成功`
        //       })
        //       this.init()
        //     }
        //   })
        //   .catch((e) => {
        //     console.log(e)
        //   })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: `已取消删除`
        })
      })
    },
    // Tip 跳到《出库单信息》页面
    handleDetail(item) {
      this.$router.push({
        name: 'OutBoundDelivery',
        params: {
          billNumber: item.billNumber
        },
        // Tip 携带的额外参数(出库信息的单号跟类型)，方便从《出库单信息》页面 跳回到《出库信息详情》页面
        query: {
          billNumber: this.params.billNumber,
          billType: this.params.billType,
          sourcePage: 'OutBoundInfoDetail'
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // Tip 打印
    handlePrint(item, printStyle) {
      const { billNumber } = item
      const params = {
        billNumber,
        ...printStyle && { printStyle }
      }
      return new Promise((resolve, reject) => {
        printApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            window.open(process.env.VUE_APP_BASE_API_WMS + data)
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 打印
    handlePrintTemplate(item) {
      this.handlePrint(item)
      // item.type === 2 && this.handlePrint(item)
      // item.type === 1 && (this.dlgVisible = true, this.curRow = item)
    },
    // 出库
    handleOutWarehouse(item) {
      this.$router.push({
        name: 'OutBoundInfoWarehouseOut',
        params: {
          billNumber: this.params.billNumber, // 单据编号
          billType: this.params.billType // 单据类型
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>

</style>

