export default {
  data() {
    return {}
  },
  methods: {
    /*
    *   1. 修改tagsView
    *   @params t (默认为this.$route.meta.title) tags中名称
    *   @params extraTitle  tags中除了当前页面 name 额外添加的name  展示方式为 name(extraTitle)
    *   完全修改的时候只传入完成 t 即可， 需要在当前 title 中添加小括号补充参数的传递 null, extraTitle
    */
    setTagsTitleMixins(t = null, extraTitle = null) {
      let title = t || this.$route.meta.title
      const tempRoute = Object.assign({}, this.$route)
      if (extraTitle) {
        title = `${title}（${extraTitle}）`
      }
      const route = Object.assign({}, tempRoute, { title })
      this.$store.dispatch('tagsView/updateVisitedView', route)
    },
    /*
    *   2. 关闭当前页签
    */
    closeTagsMixins() {
      const tempRoute = Object.assign({}, this.$route)
      this.$store.dispatch('tagsView/delView', tempRoute)
    },
    /*
    *   3. 关闭当前页签并后退路由
    */
    closeTagsBackMixins() {
      const tempRoute = Object.assign({}, this.$route)
      this.$store.dispatch('tagsView/delView', tempRoute)
      this.$router.go(-1)
    }
  }
}
