<template>
  <div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        table-height-type="half"
        :index="true"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :table-column-sort="false"
        :show-operate="false"
        :table-selection="tableSelection"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      />
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getMaterialDetailApi, getReservationDetailApi, getReturnDetailApi } from '@/apis/modules/out-bound/outbound-information'
import { getOutsourceListApi } from '@/apis/modules/out-bound/outsourcing'
import { getBillTransferListApi } from '@/apis/modules/warehouse/discharge'

export default {
  mixins: [paginationPage],
  data() {
    return {
      loading: false,
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDesc',
          label: '物料组描述',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编码',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'needNum',
          label: '需求数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // {
        //   module: 'word',
        //   prop: 'sendNum',
        //   label: '已发数量',
        //   align: 'unit',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // },
        {
          module: 'word',
          prop: 'needDate',
          label: '需求日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inventoryNum',
          label: '库存数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'inventoryNum',
          label: '剩余出库数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: c => c.needNum - c.splitNum,
          headerAlign: 'center'
        }
      ],
      columnSets: []
    }
  },
  computed: {
    // Tip 单据类型
    billType() {
      return this.$route.params.billType
    },
    // Tip 订单号
    billNumber() {
      return this.$route.params.billNumber
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    // Tip 初始化来源单(预留单 | 领料单 | 退货单 | 外协单 | 调拨单)的物料列表
    init() {
      const api = {
        1: getReservationDetailApi, // 预留单
        2: getMaterialDetailApi, // 领料单
        3: getReturnDetailApi, // 退货单
        4: getOutsourceListApi, // 外协单
        5: getBillTransferListApi // 调拨单
      }[this.billType]
      if (!api) return
      this.loading = true
      return new Promise((resolve, reject) => {
        const params = {
          ...this.billType === '1' && { reservedNumber: this.billNumber },
          ...['2', '3'].includes(this.billType) && { requisitionNumber: this.billNumber },
          ...this.billType === '4' && { outsourceOutNumber: this.billNumber },
          ...this.billType === '5' && { transferNumber: this.billNumber }
        }
        api({
          ...params,
          page: {
            ...this.pageInfo
          }
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip 表格点击事件回调
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
