import request from '@/apis/request.wms'

// 列表
export function getDetailApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOutDetail/detail',
    method: 'post',
    data
  })
}
// 新增保存之前的数据填充
export function getAddDetailApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOut/beforeAdd',
    method: 'post',
    data
  })
}
// 保存
export function getSaveApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOut/add',
    method: 'post',
    data
  })
}
// 出库单修改保存接口
export function modifySaveApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOut/edit',
    method: 'post',
    data
  })
}
// 保存之后的详情
export function getSaveDetailApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOut/detail',
    method: 'post',
    data
  })
}
// 详情保存
export function submitDialogApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOutDetail/saveMaterialDetailInventory',
    method: 'post',
    data
  })
}
// 出库单列表
export function getListApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOut/pageList',
    method: 'post',
    data
  })
}
// 删除
export function getDeleteApi(data) {
  return request({
    url: '/outWarehouse/wmsStockOut/delete',
    method: 'post',
    data
  })
}
// 打印
export function printApi(data = {}) {
  return request({
    url: '/outWarehouse/wmsStockOut/print',
    method: 'post',
    data
  })
}
// 扫码出库
export function scanQROutWarehouseApi(data = {}) {
  return request({
    url: '/outWarehouse/wmsStockOut/scanQROutWarehouse',
    method: 'post',
    data,
    timeout: 2 * 60 * 1000 // 单位(ms)
  })
}
// 出库管理/出库单据信息 保存物料
export function saveStockOutMaterialsApi(data = {}) {
  return request({
    url: '/outWarehouse/wmsStockOut/saveStockOutMaterials',
    method: 'post',
    data
  })
}
