import request from '@/apis/request.wms'

// 主列表
export function getListApi(data) {
  return request({
    url: '/enterWarehouse/wmsStockIn/deliveryReceiptPageList',
    method: 'post',
    data
  })
}

// 主列表新增
export function addListApi(data) {
  return request({
    url: '/mes/technologyMainMaterial/add',
    method: 'post',
    data
  })
}

// 主列表修改
export function updateListApi(data) {
  return request({
    url: '/mes/technologyMainMaterial/edit',
    method: 'post',
    data
  })
}

// 主列表删除
export function deleteListApi(data) {
  return request({
    url: '/mes/technologyMainMaterial/delete',
    method: 'post',
    data
  })
}

// 打印
export function getPdfUrlApi(data) {
  return request({
    url: '/enterWarehouse/wmsStockIn/print',
    method: 'post',
    data
  })
}

// 出库管理/退货 入库单选择
export function getWarehouseWarrantApi(data) {
  return request({
    url: '/enterWarehouse/wmsStockIn/pageList4ReturnMaterials',
    method: 'post',
    data
  })
}

